.buttonUp {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 42px;
    width: 136px;
    font-weight: 500;
    font-size: 20px;
    margin-top: 40px;
    background: var(--bg-btn-color);
    color: var(--bg-color);
    border: 1px solid var(--bg-btn-hover-color);
    border-radius: 12px;
    cursor: pointer;
    &:hover {
      background-color: var(--bg-btn-hover-color);
    }
  }
  .form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
    padding: 50px 15px 0 15px;
  }
  
  .label {
    position: relative;
    display: flex;
    flex-direction: column;
  }
  
  .input {
    height: 25px;
    width: 250px;
    font-size: 20px;
    padding: 7px 10px 7px 35px;
    border-radius: 12px;
    color: var(--bg-btn-color);
    outline: none;
    background-color: transparent;
    border: 1px solid var(--bg-btn-color);
    &::placeholder {
      color: var(--bg-btn-color);
    }
    &:focus {
      border: 1px solid var(--bg-btn-hover-color);
    }
  }
  
  .iconPassword,
  .iconUser,
  .iconEmail {
    margin-left: 5px;
    stroke: 0;
    fill: var(--bg-btn-color);
    position: absolute;
    top: 14px;
    left: 10px;
  }
  .errorMessage {
    color: var(--main-text-color);
    font-size: 10px;
    margin: 0;
    padding: 0;
  }